* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  background: none;
  border: none;
  margin: 0;
}

h1,
h2,
h3 {
  margin: 0;
  padding: 0;

  font-size: 100%;
  font-style: inherit;
  font-weight: inherit;
}
